import { motion } from "framer-motion";
import styled from "styled-components";
import { mediaQueries } from "./Themes";

const Box = styled.div`
  backdrop-filter: blur(2px);
  box-shadow: 0 0 1rem 0 rgba(0, 0, 0, 0.2);
  text-decoration: none;
  width: calc(10rem + 15vw);
  height: 20rem;
  border: 2px solid ${(props) => props.theme.text};
  padding: 1rem;
  color: ${(props) => props.theme.text};

  display: flex;
  flex-direction: column;
  z-index: 5;

  &:hover {
    color: ${(props) => props.theme.body};
    background-color: ${(props) => props.theme.text};

    transition: all 0.3s ease;
  }

  ${mediaQueries(50)`
    width:calc(60vw);

  `};
  ${mediaQueries(30)`
    
    height:18rem;

  `};

  ${mediaQueries(25)`
    
    height:14rem;
    padding:0.8rem;
    backdrop-filter: none;

  `};
`;

const ImgLink = styled.img`
  width: 100%;
  height: 100%;
`;


const Title = styled.h3`
  color: inherit;
  padding: 0.5rem 0;
  padding-top: 1rem;
  font-family: "Karla", sans-serif;
  font-weight: 700;
  ${mediaQueries(40)`
    font-size:calc(0.8em + 1vw);

  `};

  ${mediaQueries(25)`
    
    font-size:calc(0.6em + 1vw);



  `};

  border-bottom: 1px solid ${(props) => props.theme.text};

  ${Box}:hover & {
    border-bottom: 1px solid ${(props) => props.theme.body};
  }
`;
const HashTags = styled.div`
  padding: 0.5rem 0;
  ${mediaQueries(25)`
    font-size:calc(0.5em + 1vw);
  `};
`;
const Tag = styled.span`
  padding-right: 0.5rem;
`;


const Container = styled(motion.div)``;
const item = {
    hidden: { scale: 0 },
    show: { scale: 1, transition: { type: "spring", duration: 0.5 } },
};

const BlogComponent = (props) => {
    const { name, tags, fronturl, backurl, imgSrc, link } = props.blog;
    return (
        <Container variants={item}>
            <Box target="_blank">
                <a href={link}><ImgLink src={imgSrc} /></a>
                <Title>{name}</Title>
                <HashTags>
                    {tags.map((t, id) => (
                        <Tag key={id}>#{t}</Tag>
                    ))}
                </HashTags>
                {backurl ?
                    <div><a href={fronturl}>Reposit贸rio Frontend</a> <br/> <a href={backurl}>Reposit贸rio Backend</a></div> :
                    <div><a href={fronturl}>Reposit贸rio Frontend</a></div>
                }
            </Box>
        </Container>
    );
};

export default BlogComponent;
